/* MENU */
  
.profile {
  width: 512px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 0px 0px 4px 4px;
  }
  
.description {
    text-align: center;
}

.name {
    color: #212121;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.18;
    margin-top: 30px;
    margin-bottom: 10px;
    text-align: center;
}

.tag {
    font-size: 16px;
    line-height: 1.18;
    letter-spacing: 0.03em;
    margin-top: 0px;
    margin-bottom: 16px;
    text-align: center;
}

.location  {
    font-size: 16px;
    line-height: 1.18;
    letter-spacing: 0.03em;
    margin-top: 0px;
    margin-bottom: 16px;
    text-align: center;
}


.stats{ 
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    list-style: none;
    background-color: #dad7d7;
    padding: 25px;
    margin: 0;
   }
  .quantity {
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
  }